.header {
  position: fixed;
  z-index: 1;
  width: 100%;
  padding: 15px;
  height: 50px;
  background-color: #f14f41;
}
.sctn-hdr {
  background-color: #f4f4f4;
  border-bottom: 1px solid #e2e2e2;
  padding: 5px 10px;
}
.main {
  padding-top: 50px;
  height: 100vh;
}
.main-section {
  align-self: flex-start;
}
.main-section,
.sidebar {
  background-color: #fff;
  margin-top: 30px;
}
.sidebar {
  width: 20%;
  max-height: 90vh;
  overflow: hidden;
}
.controls-div {
  background-color: rgba(0,0,0,0.5);
  color: #fff;
}
.mobile-list {
  overflow: scroll;
  list-style-type: none;
  list-style: none;
}
.mobile-list li {
  margin: 5px;
  padding: 10px;
  border: 1px solid #fff;
}
.mobile-list li:before {
  content: '';
}
.pdf-container {
  height: 90%;
}
.audio-container {
  height: 500px;
}
.hero {
  padding: 50px;
}
path,
polygon {
  cursor: pointer;
}
polygon:hover {
  stroke-width: 5px !important;
  fill: #fff !important;
  stroke: #fff !important;
}
.part-details {
  height: auto;
  background-color: rgba(57,102,147,0.9);
  width: 100%;
  position: absolute;
}
.part-details h4 {
  padding: 0;
  margin: 0;
}
.part-details p {
  font-size: 14pt;
  font-weight: 200;
}
.mob-button {
  border: 1px solid #fff;
  background: none;
  color: #fff;
  height: 25px;
  align-self: flex-end;
  margin: 20px;
}
.progress-bar {
  border: 1px solid #fff;
  width: 100%;
  position: absolute;
  align-self: flex-end;
}
.progress-bar div {
  height: 5px;
  width: 10%;
  background: #8d0010;
}
.playerContainerv2 {
  color: #fff;
  position: relative;
}
.playerContainerv2 > div {
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 1;
}
.playerContainerv2 iframe {
  position: absolute;
  z-index: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.gradient {
  background: rgba(54,54,53,0.678);
  background: -webkit-linear-gradient(left, #000, rgba(48,48,47,0.678), rgba(0,0,0,0.929));
  background: -o-linear-gradient(left, #000, rgba(48,48,47,0.678), rgba(0,0,0,0.929));
  background: -moz-linear-gradient(left, #000, rgba(48,48,47,0.678), rgba(0,0,0,0.929));
  background: linear-gradient(to right, #000, rgba(48,48,47,0.678), rgba(0,0,0,0.929));
}
.chapter-details {
  align-self: flex-end;
}
.chapter-details h6 {
  margin: 0;
}
.chapter-details p {
  font-size: 1.1rem;
  line-height: 1.3rem;
}
.block-num {
  color: #3e3e3e;
  background-color: #f4f4f4;
  padding: 8px;
  text-align: center;
  display: inline-block;
  height: 35px;
  width: 35px;
  margin-right: 10px;
  font-weight: bold;
  font-size: 1.4em;
}
.player-btn > * {
  cursor: pointer;
  padding: 5px;
}
.captions {
  color: #494952;
  cursor: pointer;
}
.captions.focused {
  color: #fff;
}
path.focused {
  stroke: #f3fdff !important;
  opacity: 1 !important;
}
.modal {
  margin-left: 5%;
  width: 90%;
  height: 100%;
  z-index: 3;
  position: absolute;
  overflow: auto;
  background: rgba(0,0,0,0.9);
  color: #fff;
  position: fixed;
  transform: translate(0, 0);
  left: 0;
  right: 0;
  height: auto;
  top: 0;
  bottom: 0;
  z-index: 990 /* display above everything else */;
  padding: 20px;
}
.content-cover {
  position: absolute;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 2;
  background-color: #0b0b0b;
}
.prgrss-tooltip {
  border-radius: 6px;
  border: 2px solid #fff;
}
.prgrss-tooltip:hover {
  border: 2px solid #fff;
  background: #3980ce !important;
  border-radius: 8px;
  width: 16px !important;
  height: 16px !important;
}
.cognitive-embed {
  width: 100%;
  height: 100vh;
}
.ibm-btn {
  border-radius: 40px;
  font-size: 1.3rem;
  border: none;
  background-color: #3d88b8;
  color: #fff;
  padding: 15px;
}
@media only screen and (max-device-width: 700px) {
  .fluid2 {
    width: 100%;
    justify-content: flex-start;
  }
  .hero {
    padding: 0;
  }
}



